<template>
    <header class="c-header">
        <div class="header-inner">

            <div class="logo" :class="logoClass" v-if="showLogo">
                <slot name="logo">
                    <h1 @click="handleLogoClick">{{ logoText }}</h1>
                </slot>
                <div class="sidebar-toggle-desktop" v-if="showToggle && md" @click="toggleSidebar"></div>
            </div>
            <a href="#" class="sidebar-toggle-mobile" v-if="showToggle && ! md" @click="toggleSidebar"></a>
            <nav class="header-menu" v-if="md === true">
                <ul class="nav nav-header">
                    <slot name="menu"></slot>
                </ul>
            </nav>
        </div>
        <slot name="after"></slot>
        <c-side-nav ref="sidenav-header-menu" :draggable="false" v-if="md === false">
            <li><a class="subheader">Menu</a></li>
            <slot name="menu"></slot>
        </c-side-nav>
    </header>

    <!--<c-header-button v-on:click.native="toggleSidenav('projects')" title="Project" :label="project.display_name"></c-header-button>-->

</template>

<script>
    import {layout} from 'mixins'
    import {mapActions} from 'vuex'
    export default {
        name  : 'c-header',
        mixins: [layout],
        props : {
            logoHref  : {type: String, default: '/'},
            logoText  : {type: String, default: 'Codex'},
            logoClass : {type: String, default: 'logo-text'},
            showLogo  : {type: Boolean, default: true},
            showToggle: {type: Boolean, default: false},
        },


        data() {
            return {md: false}; //, logoLink: '', logoText: '', showLogo: true, showToggle: false}
        },
        mounted(){
            this.handleResize();
            this.$on('resize', this.handleResize);
            this.$$ready(() => {
                this.handleResize();
                if ( this.$refs['sidenav-header-menu'] ) {
                    document.body.appendChild(this.$refs['sidenav-header-menu'].$el)
                }
            })
        },
        beforeDestroy(){
            this.$off('resize', this.handleResize);
        },
        methods: {
            ...mapActions(['toggleSidebar']),
            handleResize(){
                this.md = this.isBreakpointUp('md');
            },
            toggleHeaderMenu(){
                console.log('toggleHeaderMenu');
                this.$refs['sidenav-header-menu'].toggle()
            },
            handleLogoClick(){
                window.document.location.replace(this.logoHref)
            }
        }
    }
</script>



