<template>
    <li class="nav-item">
        <a href="javascript:void(0)" class="nav-link" :class="{ 'nav-link-split': title, 'nav-link-icon': icon }">
            <div class="nav-link-top" v-if="title">{{ title }}</div>
            <span v-if="label">{{ label }}</span>
            <i v-if="icon" :class="'fa fa-' + icon"></i>
            <slot></slot>
        </a>
    </li>
</template>

<script>
    import {getRandomId} from '@radic/util'
    //    import VBtn from 'bootstrap-vue/components/menu-item.vue'
    export default {
        name : 'c-header-menu-item',
        props: {
            label: {type: [Boolean, String], default: false},
            icon : {type: [Boolean, String], default: false},
            title: {type: [Boolean, String], default: false},
        },
        data(){
            return {
                id: getRandomId(5)
            }
        },
        watch: {
            $route(){
                $('#' + this.id).sideNav('hide')
            }
        }
    }
</script>



