<template>
    <transition name="fade">

        <div>
            <nprogress-container></nprogress-container>

            <slot></slot>
        </div>
    </transition>

</template>

<script>
    import 'assets/img/features/laravel.svg'
    import 'assets/img/features/fa-puzzle-piece-140x140-37474f.png'
    import 'assets/img/avatar.jpg'
    import NprogressContainer from 'vue-nprogress/src/NprogressContainer.vue'
    export default {
        name      : 'c-theme',
        components: {
            NprogressContainer
        },
        data(){
            return {show: false}
        },
        mounted(){
            this.show = true;
        }
    }

</script>


<style lang="scss" rel="stylesheet/scss">
    $webpack-build: true;
    $font-path: "../../assets/fonts";
    $fa-font-path: $font-path + '/FontAwesome';

    @import "../../styles/codex";
    @import "../../styles/prismjs";
    @import "../../styles/vendor/prism/toolbar";
    @import "../../styles/vendor/prism/line-numbers";
    @import "../../styles/vendor/prism/wpd";
    @import "../../styles/vendor/prism/command-line";
    @import "../../styles/vendor/prism/autolinker";

    @import "../../styles/codex.page.welcome";
    @import "../../styles/themes/codex-default";

    $image-path:				"../../assets/img/jstree/";
    @import "../../styles/codex.page.phpdoc";
    @import "../../styles/codex.phpdoc.document";
</style>



