<template>
    <li class="avatar" :class="classes">
        <img :src="src" class="circle">
        <slot></slot>
        <span class="secondary-content">
            <slot name="secondary"></slot>
        </span>
    </li>
</template>

<script>
    import CollectionItem from 'mixins/collection-item'

    export default {
        mixins: [
            CollectionItem
        ],

        props: {
            src: {
                type    : String,
                required: true
            }
        },

        computed: {
            classes () {
                return this.collectionsMixin
            }
        }
    }
</script>



